<template>
  <v-container>
    <v-row>
      <v-col class="mb-4">
        <h1 class="display-2 font-weight-bold mb-3">
          Alta de Vendedores
        </h1>
      </v-col>
    </v-row>
    <v-row>
      <v-col
        cols="12"
        md="8"
      >
        <form @submit.prevent="guardarVendedor()">
          <v-text-field
            v-model="seller.name"
            label="Vendedor"
            :counter="50"
            outlined
            prepend-icon="mdi-account-tie"
            required
          />
          <v-text-field
            v-model="seller.document"
            label="DNI"
            :counter="8"
            :rules="dniRules"
            outlined
            prepend-icon="mdi-label"
            required
          />
          <v-text-field
            v-model="seller.type"
            label="Categoria"
            :counter="20"
            outlined
            prepend-icon="mdi-lock"
            required
          />

          <v-card-actions>
            <v-btn
              to="/ventas/vendedores"
              color="blue-grey"
              class="mr-4"
            >
              Cancelar
            </v-btn>
            <v-btn
              type="submit"
              color="green"
              class="mr-4"
            >
              Guardar
            </v-btn>
          </v-card-actions>
        </form>
      </v-col>
    </v-row>
  </v-container>
</template>
<script>
  import axios from 'axios'

  const urlVendedor = 'https://sistema-adv.herokuapp.com/api/seller'

  export default {
    name: 'CrearVendedor',
    data () {
      return {
        seller: {
          name: '',
          type: '',
          document: '',
        },
        dniRules: [
          v => !!v || 'DNI es requerido',
          v => v.length <= 8 || 'DNI no debe ser mayor a 8 dígitos',
        ],
      }
    },
    mounted () {

    },
    methods: {
      guardarVendedor () {
        const router = this.$router
        const params = this.seller
        axios
          .post(urlVendedor, params)
          .then(res => {
            // console.log(res);
            router.push('/ventas/vendedores')
            this.limpiarCampos()
          })
      },
      limpiarCampos () {
        this.seller = ''
      },
    },
  }
</script>
